* {
  margin: 0;
  padding: 0;
}

html {
  --active-color: #00acdc;
}

body {
  background-color: #dbd5d5;
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  width: 100%;
}

.container {
  justify-content: space-between;
  margin: auto 50px;
  display: flex;
}

.moyo-logo {
  align-items: center;
  display: flex;
}

.nav-list {
  flex-direction: row;
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav-item {
  text-transform: uppercase;
  font-size: 12px;
  position: relative;
}

.nav-link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav-item :hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  inset: auto 0 0;
}

